import React from 'react';
import './App.css';
import danceClass from './danceClass.jpg';

function ClassesCard(props) {
    return(
        <div className="cardObj">
            <div className="overflow">
                <img className="danceImg" src={props.img} alt="danceImg" />
                <h2 className="cardTitle">{props.title}</h2>
                <p className="paraCard">{props.para}</p>
                <button className="cardButton">See More</button>
            </div>
        </div>
    )
}

export default ClassesCard;